import React from "react";
import {connect} from "react-redux";
import store from 'src/store';


//position:"absolute",top:topper,left:0,
const ActivityHeader = (props) => {
    let stateData = store.getState();
    let mainStyle = {width:"100%",display:"flex",flexDisplay:"row",flexJustify:"space-between"};

    mainStyle = Object.assign({},
        mainStyle,
        stateData.appSettings.activityHeadColor ? {backgroundColor:stateData.appSettings.activityHeadColor} : {},
        props.height ? {height:props.height} : {}
        );

    return (
        <div className="activityHeader pl-4 pt-2" style={mainStyle} id={props.id}>
            <div style={{display:"flex",width:"100%",justifyContent:"space-between"}}>
                <div className="activityHeaderTitle" style={{width:"25%"}}>{props.headerTitle}</div>
                <div style={{width:"75%",display:"flex",flexDirection:"row",justifyContent:"space-around"}}>
                    {props.children}
                </div>

            </div>
        </div>
    )
};

const mapStateToProps = (state) =>{
    return {
        appSettings:state.appSettings
    };
};

export default connect(mapStateToProps,{})(ActivityHeader);
